body{
    padding: 0;
    margin: 0;
    font-family: 'Anton', sans-serif;
    color: black;
    font-size: 16px;
}

* {
    box-sizing: border-box;
}

h1,h2,h3,h4,h5,h6 {
    padding: 0;
    margin: 0;
    text-transform: uppercase;
}

h2{
    font-size: 50px;
    margin-bottom: 58px;
}

h3{
    font-size: 25px;
    margin-bottom: 28px;
}



a {
    text-decoration: none;
}

ul,li {
    display: block;
    padding: 0px;
    margin: 0px;
}

p{
    margin: 0px;
}